import Accueil from './components/Accueil.vue'
import Controls from './components/Controls.vue'
import UserInterface from './components/UserInterface.vue'
import Commands from './components/Commands.vue'
import Blips from './components/Blips.vue'
import Jobs from './components/Jobs.vue'





export default [{
        path: '/vue-routing-tuto/',
        component: Accueil
    },
    {
        path: '/vue-routing-tuto/Controls',
        component: Controls,
        props: true,
    },
    {
        path: '/vue-routing-tuto/UserInterface',
        component: UserInterface,
    },
    {
        path: '/vue-routing-tuto/Commands',
        component: Commands,
    },
    {
        path: '/vue-routing-tuto/Blips',
        component: Blips,
    },
    {
        path: '/vue-routing-tuto/Jobs',
        component: Jobs,
    },

]